@import url('https://fonts.googleapis.com/css?family=Ubuntu:400,500,700&display=swap');

html{
    box-sizing: border-box;
}

*, *::before, *::after{
    box-sizing: inherit;
    margin: 4px;
    padding: 0;
}

body{
    font-family: 'Ubuntu', sans-serif;
    padding: 0;
    margin: 0;
    background-color: rgb(211, 225, 241);
    color: rgb(97, 95, 95);
}